import React from "react";
import Bookmark from "../../common/bookmark/Bookmark";
import Table /*, {TableBody, TableHeader} */ from "../../common/table";
import PropTypes from "prop-types";
import Qualities from "../qualities";
import { Link } from "react-router-dom/cjs/react-router-dom.min";

const UsersTable = ({ users, onSort, selectedSort, onToggleBookMark, onDelete }) => {
  const columns = {
    name: {
      path: "name",
      name: "",
      component: (user) => (
        <Link to={`/users/${user._id}`}>{user.name}</Link>
      ),
    },
    qualities: {
      name: "",
      component: (user) => (
        <Qualities qualities={user.qualities} />
      ),
    },
    professions: { path: "profession.name", name: "" },
    completedMeetings: { path: "completedMeetings", name: ", " },
    rate: { path: "rate", name: "" },
    bookmark: {
      path: "bookmark",
      name: "",
      component: (user) => (
        <Bookmark
          status={user.bookmark}
          onClick={() => onToggleBookMark(user._id)}
        />
      ),
    },
    delete: {
      component: (user) => (
        <button
          onClick={() => onDelete(user._id)}
          type="button"
          className="btn btn-danger"
        >
          Delete
        </button>
      ),
    },
  };

  return (
    // <Table>
    //   <TableHeader onSort={onSort} selectedSort={selectedSort} columns={columns} />
    //   <TableBody data={users} columns={columns} />
    // </Table>
    <Table
      onSort={onSort}
      selectedSort={selectedSort}
      columns={columns}
      data={users}
    />
  );
};

UsersTable.propTypes = {
  users: PropTypes.array.isRequired,
  onSort: PropTypes.func.isRequired,
  selectedSort: PropTypes.object.isRequired,
  onToggleBookMark: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};

export default UsersTable;
