import React, { useEffect, useState } from "react";
import { validator } from "../../../utils/validator";
import TextField from "../../common/form/textField/TextField";
import API from "../../../api";
import SelectField from "../../common/form/selectField/SelectField";
import RadioField from "../../common/form/radioField/RadioField";
import MultiSelectField from "../../common/form/multiSelectField/MultiSelectField";
import CheckBoxField from "../../common/form/checkBoxField/checkBoxField";

const RegisterForm = () => {
  const [data, setData] = useState({
    email: "",
    password: "",
    profession: "",
    sex: "male",
    qualities: [],
    license: false,
  });
  const [errors, setErrors] = useState({});
  const [professions, setProfessions] = useState();
  const [qualities, setQualities] = useState({});

  useEffect(() => {
    API.professions.fetchAll().then((data) => {
      const professionsList = data.map((item) => ({
        label: item.name,
        value: item._id,
      }));
      setProfessions(professionsList);
    });

    API.qualities.fetchAll().then((data) => {
      const qualitiesList = Object.keys(data).map((optionName) => ({
        value: data[optionName]._id,
        label: data[optionName].name,
        color: data[optionName].color,
      }));
      setQualities(qualitiesList);
    });
  }, []);

  const handleChange = (target) => {
    setData(prevState => ({
      ...prevState,
      [target.name]: target.value,
    }));
  };

  const validatorConfig = {
    email: {
      isRequired: {
        message: "    ",
      },
      isEmail: {
        message: "Email  ",
      },
    },
    password: {
      isRequired: {
        message: "   ",
      },
      isCapitalSymbol: {
        message: "       ",
      },
      isContainDigit: {
        message: "      ",
      },
      min: {
        message: "     8 ",
        value: 8,
      },
    },
    profession: {
      isRequired: {
        message: "   ",
      },
    },
    license: {
      isRequired: {
        message: "         ",
      },
    },
  };

  useEffect(() => {
    validate();
  }, [data]);

  const validate = () => {
    const errors = validator(data, validatorConfig);
    setErrors(errors);
    return Object.keys(errors).length === 0;
  };
  const isValid = Object.keys(errors).length === 0;

  const handleSubmit = (evt) => {
    evt.preventDefault();
    const isValid = validate();
    if (!isValid) return;
    console.log(data);
    console.log(errors);
  };

  if (professions && Object.keys(qualities).length > 0) {
    return (
      <form onSubmit={handleSubmit}>
        <TextField
          label=" "
          id="email"
          name="email"
          value={data.email}
          onChange={handleChange}
          error={errors.email}
        />
        <TextField
          label=""
          type="password"
          id="password"
          name="password"
          value={data.password}
          onChange={handleChange}
          error={errors.password}
          autocomplete="off"
        />
        <SelectField
          label="  "
          name="professions"
          defaultOption="Choose..."
          options={professions}
          onChange={handleChange}
          value={data.profession}
          error={errors.profession}
        />
        <RadioField
          options={[
            { name: "Male", value: "male" },
            { name: "Female", value: "female" },
            { name: "Other", value: "other" },
          ]}
          value={data.sex}
          name="sex"
          onChange={handleChange}
          label="  "
        />
        <MultiSelectField
          options={qualities}
          onChange={handleChange}
          name="qualities"
          defaultValue={data.qualities}
          label="  "
        />
        <CheckBoxField
          value={data.license}
          name="license"
          onChange={handleChange}
          error={errors.license}
        >
           <a> </a>
        </CheckBoxField>
        <button
          type="submit"
          disabled={!isValid}
          className="btn btn-primary w-100 mx-auto"
        >
          Submit
        </button>
      </form>
    );
  } else {
    return <p>Loading...</p>;
  }
};

export default RegisterForm;
